import React, { useEffect, useRef } from "react";

interface Prop {
  closeModal: () => void;
}
const NewImageModal: React.FC<Prop> = React.forwardRef(
  ({ closeModal }): JSX.Element => {
    const [Open, setOpen] = React.useState(false);
    const [showModal, setShowModal] = React.useState(false);

    const myRef = useRef(null);

    const handleClickOutside = (e) => {
      if (!myRef.current.contains(e.target)) {
        // setShowModal(false);
        closeModal();
      }
    };

    useEffect(() => {
      document.addEventListener("mousedown", handleClickOutside);
      return () =>
        document.removeEventListener("mousedown", handleClickOutside);
    });

    return (
      <>
        <div className="absolute z-50 flex items-center justify-center w-screen h-screen overflow-x-hidden overflow-y-auto outline-none focus:outline-none">
          <div
            ref={myRef}
            className="relative w-auto max-w-3xl mx-auto my-6"
            style={{ height: "90vh" }}
          >
            {/*content*/}
            <div className="relative flex flex-col w-full bg-white border-0 rounded-lg shadow-lg outline-none focus:outline-none">
              {/*header*/}
              <div className="flex items-start justify-between p-5 border-b border-solid rounded-t border-blueGray-200">
                <button>
                  <svg
                    width="28"
                    height="16"
                    viewBox="0 0 28 16"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M7.33333 13.5L2 8M2 8L7.33333 2.5M2 8L26 8"
                      stroke="#4B5563"
                      stroke-width="4"
                      stroke-linecap="round"
                      stroke-linejoin="round"
                    />
                  </svg>
                </button>
                <p className="text-xs not-italic font-medium leading-5 font-inter">
                  Select Images
                </p>
                <button
                  className="float-right p-1 ml-auto text-3xl font-semibold leading-none text-black bg-transparent border-0 outline-none opacity-5 focus:outline-none"
                  onClick={() => setShowModal(false)}
                >
                  <span className="block w-6 h-6 text-2xl text-black bg-transparent outline-none opacity-5 focus:outline-none">
                    Ã—
                  </span>
                </button>
              </div>
              {/*body*/}
              <div className="relative flex-auto p-6">
                <div>
                  <h2 className="text-3xl not-italic font-bold leading-9 font-inter text-">
                    Upload New Image
                  </h2>
                </div>
              </div>
              <div
                style={{ width: "699px" }}
                className="border-2 border-dashed rounded-md h-72 "
              ></div>
              {/*footer*/}
              <div className="flex items-center justify-end p-6 border-t border-solid rounded-b border-blueGray-200">
                <button
                  className="px-6 py-2 mb-1 mr-1 text-sm font-bold text-red-500 uppercase transition-all duration-150 ease-linear outline-none background-transparent focus:outline-none"
                  type="button"
                  onClick={() => setShowModal(false)}
                >
                  Close
                </button>
                <button
                  className="px-6 py-3 mb-1 mr-1 text-sm font-bold text-white uppercase transition-all duration-150 ease-linear rounded shadow outline-none bg-emerald-500 active:bg-emerald-600 hover:shadow-lg focus:outline-none"
                  type="button"
                  onClick={() => setShowModal(false)}
                >
                  Save Changes
                </button>
              </div>
            </div>
          </div>
        </div>
        <div className="fixed inset-0 z-40 bg-black opacity-25"></div>
      </>
    );
  }
);
export default NewImageModal;
